.container-slider {
    max-width: 100%;
    height: 600px;
    margin: 0 auto 0;
    position: relative;
    top: 100px;
    overflow: hidden;
  }
  @media screen and (max-width: 700px){
    .container-slider {
      margin: 100px 10px 0;
    }
  }
  .slide {
    width: 100%;
    height: 100%;
    position: absolute;
    opacity: 0;
    transition: opacity ease-in-out 0.4s;
  }
  .slide img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  .active-anim {
    opacity: 1;
  }
  
  .btn-slide {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background: #f1f1f1;
    border: 1px solid rgba(34, 34, 34, 0.287);
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }
  
  .btn-slide img {
    width: 25px;
    height: 25px;
    pointer-events: none;
  }
  .prev {
    top: 50%;
    left: 20px;
    transform: translateY(-60%);
  }
  .next {
    top: 50%;
    right: 20px;
    transform: translateY(-60%);
  }
  
  
  .container-dots {
    position: absolute;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
  }
  .dot {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 3px solid #f1f1f1;
    margin: 0 5px;
    background: #f1f1f1;
  }
  .dot.active {
    background: rgb(32, 32, 32);
  }
  

  .head_button {
      position: absolute;
      padding: 10px 60px;
      border: none;
      border-radius:30px;
      background:rgb(40, 60, 236);
      color: #fff;
      font-size:20px;
      top: 44rem;
      left: 17rem;
      
      transition: all 0.3s;
      &:hover {
        background:rgb(55, 0, 255) !important;
        
      }

  }

  .head_button_container {
      position: relative;

      h1 {

          position: absolute;
          top: 30rem;
          left: 14rem;
          color: rgba(255, 255, 255, 0.747);
      }

      p {
        position: absolute;
        top: 36rem;
        left: 10rem;
        width: 420px;
        color: rgba(255, 255, 255, 0.747);
      }
  }